.profile_page {
  .user_book {
    padding: 20px;
    margin-top: 30px;
    border-top: 1px solid var(--border);
    display: flex;
    flex-direction: column;
    gap: 20px;

    .header {
      display: flex;
      justify-content: space-between;
    }
    button {
      @extend %button;
      height: 30px;
    }
  }
}

@media screen and (max-width: 670px) {
  .profile_page {
    .user_book {
      padding: 0;
      position: relative;
      padding-bottom: 100px;

      .btn_update {
        height: auto;
        padding: 5px auto;
      }
      .read_more button {
        width: 100%;
        position: absolute;
        left: 0;
      }
      .header {
        display: flex;
        flex-direction: column;
        h3 {
          text-align: center;
          margin-top: 20px;
        }
      }
      button {
        height: 40px;
        margin: 20px 0;
      }
    }
  }
}
