.bookstore {
  .card_option {
    height: 160px;
    width: 100%;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    transition: all 0.3s ease-in-out;
    position: relative;
    cursor: pointer;
    margin-bottom: 30px;

    &:hover {
      box-shadow: 0 0 30px rgba(44, 44, 44, 0.164);
      transition: all 0.3s ease-in-out;
    }

    .text_content {
      position: absolute;
      bottom: 15px;
      left: 15px;
      padding: 10px;
      p {
        color: #ffffff;
        font-size: 15px;
        font-weight: 500;
      }
      p.short {
        font-size: 30px;
        font-weight: bold;
        letter-spacing: 2px;
      }
    }
  }
  .cards {
    display: flex;
    flex-direction: column;
    gap: 50px;
  }
}

@media screen and (max-width: 950px) {
  .bookstore {
    .card_option {
      height: 120px;
    }
    .data_not_found {
      h2 {
        font-size: 15px;
        font-weight: 500;
      }
      img {
        width: 80% !important;
      }
    }
  }
}
