.select_custom {
  position: relative;

  input {
    @include input(40px);
  }
  button {
    position: absolute;
    right: 0;
    top: 5px;
    height: 40px;
    width: 40px;
    @extend %button;
    background-color: transparent !important;
    color: var(--primary);
  }
  .options {
    border: 1px solid var(--border);
    max-height: 150px;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 0 10px;
    transition: all 0.3s ease-in-out;
    margin-bottom: 15px;

    .option {
      padding: 5px;
      background-color: var(--background);
      margin: 5px 0;
      cursor: pointer;
      padding-left: 8px;
      transition: all 0.3s ease-in-out;

      &:hover {
        background-color: var(--accent);
      }
    }
  }
  .options.hide {
    transition: all 0.3s ease-in-out;
    max-height: 0;
    border-color: transparent;
  }
}

