.get_access_book {
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.1921568627);
  z-index: 2;

  .fa-close {
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer;
  }
  .access_book {
    background-color: var(--background);
    border: 1px solid var(--border);
    padding: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 500px;

    h3 {
      grid-column: span 2;
      text-align: center;
    }

    label {
      margin: 20px 0 10px 0;
      grid-column: span 2;
    }
    input {
      @include input(40px);
    }
    button {
      @extend %button;
      float: right;
      grid-column: span 2;
    }
    a {
      grid-column: span 2;
      grid-column-start: 2;
      text-align: right;
      margin-top: 10px;
    }
    #matricule_error {
      color: var(--danger);
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 670px) {
  .get_access_book {
    .access_book {
      width: 90%;
    }
  }
}
