#aside_bar {
  z-index: 2;
  min-width: 300px;
  width: 300px;
  height: 100vh;
  background: var(--background);
  position: sticky;
  top: 0;
  padding: 4em 2em 40px 2em;
  transition: all 0.3s ease;
  border-left: 1px solid var(--border);
  border-radius: 0;

  .overlay {
    display: none;
    height: inherit;
    background-color: var(--background);
    opacity: 0.6;
    width: 100vw;
    position: absolute;
    top: 0;
    right: 301px;
  }
  .submit_filter {
    display: none;
    justify-content: center;
    align-items: center;
    border-radius: 25px 10px 15px 10px;
    background-color: var(--success);
    position: absolute;
    right: 20px;
    bottom: 20px;
    height: 45px;
    padding: 0 20px;
    cursor: pointer;
    color: #ffffff;
    transition: 0.3s linear;
    i {
      color: #ffffff;
    }
    &:active {
      transform: scale(0.9);
    }
  }

  & > .content {
    overflow-x: hidden;
    overflow-y: scroll;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    transition: 0.3s ease-in-out;
    &::-webkit-scrollbar-thumb {
      background-color: transparent;
      max-width: 5px;
    }
    &::-webkit-scrollbar-track {
      background-color: transparent;
      padding: 2px;
      box-shadow: inset 0 0 4px transparent;
    }
  }

  .mobile_top_aside {
    display: none;
    @extend %center;
    position: absolute;
    left: 10px;
    top: 6px;
    height: 40px;
    width: 40px;
    background-color: var(--primary);
    color: var(--writing);
    cursor: pointer;
    transition: all 0.2s ease;
    i {
      color: #ffffff;
    }
  }
  form {
    .search_group {
      @extend %search_group;
      i {
        background-color: var(--accent);
        border: 1px solid var(--border);
        transition: 0.2s linear;
        height: 35px;
        width: 35px;
        line-height: 35px;
        padding: auto;
        border-radius: 5px;
        text-align: center;
      }
    }
  }

  &.close {
    margin-left: 50px;
    transform: translateX(300px);
    .mobile_top_aside {
      left: -60px;
    }
  }
}

@media screen and (max-width: 950px) {
  #aside_bar {
    position: fixed;
    right: 0;
    .mobile_top_aside {
      display: flex;
    }
    &.close {
      margin-right: 0;
      transform: translateX(310px);
      .overlay {
        display: none;
      }
      .submit_filter {
        opacity: 0;
        pointer-events: none;
      }
    }
    .overlay {
      display: block;
    }
    .submit_filter {
      display: flex;
    }
  }
}

