.home_page {
  h2 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 30px;

    span {
      color: var(--primary);
    }
  }
  .cards {
    display: flex;
    flex-direction: column;
    gap: 50px;
    position: relative;
    min-height: 70vh;

    .card_option {
      height: 200px;
      width: 100%;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      transition: all 0.3s ease-in-out;
      position: relative;
      cursor: pointer;

      &:hover {
        box-shadow: 0 0 30px rgba(44, 44, 44, 0.164);
        transition: all 0.3s ease-in-out;
      }

      .text_content {
        position: absolute;
        bottom: 15px;
        left: 15px;
        padding: 10px;
        p {
          color: #ffffff;
          font-size: 18px;
          font-weight: 500;
          text-transform: capitalize;
        }
        p.short {
          font-size: 30px;
          font-weight: bold;
          letter-spacing: 4px;
        }
      }
    }
  }
}

@media screen and (max-width: 950px) {
  .home_page {
    h2 {
      font-size: 24px;
      margin-bottom: 10px;
      margin-top: -20px;
    }
  }
}
