.start_component {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #5e5e5e8e;
  top: 0;
  position: fixed;
  z-index: 2;
  border-radius: 0;

  .content_slide {
    background-color: var(--background);
    height: 460px;
    max-height: 90vh;
    width: fit-content;
    padding: 10px;
    border: 1px solid var(--borber);
    display: grid;
    grid-template-rows: 90% auto;

    .welcome {
      @extend %center;
      flex-direction: column;
      .illustration {
        @extend %center;
      }
      img {
        height: calc(100% - 10px);
        max-height: 280px;
      }
      .text {
        text-align: center;
      }
    }
    .welcome.end {
      padding: 10px;
    }
    .cycle_stape {
      height: 100%;
      padding: 20px;
      display: grid;
      grid-template-rows: 20px calc(100% - 80px) 60px;

      img {
        height: 100%;
        margin: auto;
      }

      .drop_tabs {
        border: 1px solid var(--border);

        .title_bar {
          padding: 0 10px;
          display: none;
        }
        .content {
          max-width: none;
          justify-content: space-around;
        }
        .content .item {
          padding: 10px;
        }
      }
      .text {
        text-align: center;
        text-transform: capitalize;
        font-weight: 500;
      }
    }

    .theme {
      display: flex;
      flex-direction: column;
      h3 {
        width: 100%;
        text-align: center;
        margin: 20px 0;
        text-transform: capitalize;
      }
    }

    .navigation {
      @extend %center;
      justify-content: space-between;
      position: relative;
      padding: 0 10px;

      .stapes {
        display: flex;
      }
      .stape {
        margin: 0 2px;
        height: 10px;
        width: 10px;
        background-color: gray;
        cursor: pointer;
      }
      .stape.active {
        background-color: var(--primary);
      }
      .next.disable {
        background-color: var(--secondary);
        cursor: not-allowed;
      }
      .next {
        @extend %button;
        height: 30px;
        // position: absolute;
        // right: 10px;
      }
      .prev {
        @extend %button-outline;
        // position: absolute;
        // left: 10px;
        height: 30px;
        color: var(--writing);
        i {
          color: var(--writing);
        }
      }
      i {
        color: #ffffff;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .start_component {
    background-color: var(--background);

    .welcome .welcome.end {
      padding: 0;
    }
    .content_slide {
      width: 100vw;
      height: 100%;
      padding: 0;
      border-radius: 0;
      grid-template-rows: auto 50px;
      .cycle_stape {
        padding: 0;
        padding-bottom: 20px;
        grid-template-rows: 60px auto 60px;
        img {
          width: 80%;
          height: auto;
        }
        .text {
          padding: 10px 20px;
        }
      }
      .welcome {
        padding: 0;
        padding-bottom: 20px;

        img {
          width: 80%;
          height: auto;
          margin: auto;
        }
      }
      .navigation {
        padding: 10px 20px;
      }
    }
  }
}
@media screen and (max-width: 330px) {
  .start_component {
    .content_slide {
      .navigation {
        justify-content: center;
        gap: 8px;
      }
    }
  }
}
