.theme_selector {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  img {
    height: 150px;
    max-width: 300px;
    border: 2px solid var(--border);
    cursor: pointer;
  }
}

@media screen and (max-width: 1080px) {
  .theme_selector {
    align-items: center;
    flex-direction: column;
    gap: 10px;
  }
}
@media screen and (max-width: 950px) {
  .theme_selector {
    flex-direction: row;
    gap: 0;
  }
}

@media screen and (max-width: 750px) {
  .theme_selector {
    h3 {
      margin-bottom: 0;
    }
    flex-direction: column;
    gap: 10px;
  }
}
