.add_book_form {
  display: grid;
  grid-template-columns: 200px auto;
  grid-gap: 30px;
  padding: 20px;
  padding-top: 40px;
  margin-top: 27px;
  // border-top: 1px solid var(--border);
  position: relative;

  .alert_box {
    grid-column: span 2;
    border: 1px solid var(--warning);
    padding: 10px;
    position: relative;
    display: none;

    i {
      position: absolute;
      cursor: pointer;
      top: 10px;
      right: 10px;
      height: 25px;
      width: 25px;
      line-height: 25px;
      background-color: var(--accent);
      border-radius: 50%;
      @extend %center;

      &:hover {
        color: var(--secondary);
      }
    }
  }

  input,
  textarea {
    @include input(40px);
  }

  sup {
    font-size: large;
    color: var(--danger);
    line-height: 4px;
  }

  .help_message {
    font-size: 14px;
    display: block;
    color: var(--danger);
    opacity: 0.8;
    margin-bottom: 10px;
  }

  .files_upload {
    margin-top: 30px;
    .is_update_file {
      label {
        display: flex;
        align-items: center;
        padding: 0 5px;
        margin: 5px 0 20px 0;
        height: 30px;
        width: 100%;
        cursor: pointer;
        border-radius: 8px;
        background-color: var(--accent);

        .commutateur {
          height: 70%;
          width: 50%;
          border-radius: 8px;
          background-color: var(--secondary);
          text-align: center;
          color: #ffffff;
          line-height: 21px;
        }
      }
      #is_update:checked + label {
        background-color: var(--border);
        justify-content: flex-end;
      }
      #is_update:checked + label .commutateur {
        background-color: var(--primary);
      }
    }
    .disable {
      pointer-events: none;
      opacity: 0.2;
    }
    .lable_file {
      display: grid;
      grid-template-columns: 60px auto;
      height: 100px;
      width: 100%;
      border: 1px dashed #9b9b9b;
      border-radius: 5px;
      cursor: pointer;
      position: relative;

      i {
        margin: -1px 0 0 -1px;
        border: 1px solid #9b9b9b;
        border-radius: 4px 0 0 4px;
        height: calc(100% + 2px);
        width: 50px;
        font-size: 22px;
        @extend %center;
      }
      p {
        height: 100%;
        line-height: 100%;
        @extend %center;
        justify-content: flex-start;
        word-wrap: break-word;
        overflow: hidden;
      }
    }
  }
  .field {
    display: flex;
    flex-direction: column;
    gap: 0;
    padding-top: 0;

    .tags {
      display: grid;
      grid-gap: 10px;
      height: -moz-fit-content;
      height: fit-content;
      grid-template-columns: auto auto auto;
      margin: 20px 0;
      background: var(--background);
      padding: 10px;
      border-radius: 5px;
      border: 1px solid var(--border);

      .level_radio:checked + label {
        background-color: var(--primary);
        border-color: var(--primary);
      }

      .item {
        background-color: var(--background);
        border: 1px solid var(--border);
        padding: 0 10px;
        border-radius: 5px;
        animation: show_toast_mobile 0.5s linear;
        cursor: pointer;
        min-width: 20%;
        text-align: center;
        display: grid;
        grid-template-columns: 20px auto;
      }
      span {
        width: 100%;
        display: block;
      }
    }
    h3 {
      margin-bottom: 20px;
    }

    textarea {
      resize: vertical;
      min-height: 40px;
      height: 100px;
      padding-top: 10px;
    }
  }
  .add_book_btn {
    display: flex;
    margin-top: 20px;
    justify-content: space-between;

    .cancel {
      @extend %button-outline;
      border-color: var(--danger);
    }
    .submit_book {
      @extend %button;
    }
  }
}

@media screen and (max-width: 570px) {
  .add_book_form {
    padding: 5px;
    display: flex;
    flex-direction: column;

    .files_upload {
      width: 100%;
      margin: auto;

      .lable_file {
        height: 50px;
        margin: 15px 0;
      }
    }
  }
}
@media screen and (max-width: 360px) {
  .add_book_form {
    .add_book_btn {
      display: flex;
      flex-direction: column-reverse;

      button {
        margin: 6px 0;
      }
    }
  }
}
